.worldmap-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: linear-gradient(180deg, #dff5ff 0%, #ffffff 100%);
  min-height: 100vh;
}

.worldmap-image img {
  width: 80%;
  max-width: 700px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 40px;
}

.continents-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
}

.continent-card {
  text-align: center;
  cursor: pointer;
  background: white;
  border-radius: 15px;
  padding: 15px;
  width: 120px;
  transition: 0.3s ease-in-out;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.continent-card:hover {
  background: #e6f7ff;
  transform: translateY(-5px);
}

.continent-icon {
  width: 80px;
  height: 80px;
  object-fit: contain;
}

.continent-card p {
  margin-top: 8px;
  font-size: 16px;
  font-weight: 600;
  color: #333;
}
/* âœ¨ Fun hover glow */
.continent-card:hover {
  box-shadow: 0 0 20px rgba(0, 200, 255, 0.5);
  background: linear-gradient(180deg, #f8fdff 0%, #e6f7ff 100%);
}
.section-title {
  text-align: center;
  margin: 25px 0 15px 0;
  font-size: 24px;
  color: #0b3b64;
  font-weight: 700;
}

.subjects-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  margin-top: 10px;
}

.subject-card {
  text-align: center;
  cursor: pointer;
  background: white;
  border-radius: 15px;
  padding: 15px;
  width: 130px;
  transition: 0.3s ease-in-out;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.subject-card:hover {
  background: #e6f7ff;
  transform: translateY(-5px);
}

.subject-icon {
  width: 90px;
  height: 90px;
  object-fit: contain;
}

.subject-card p {
  margin-top: 8px;
  font-size: 16px;
  font-weight: 600;
  color: #333;
}
.music-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: #0b3b64;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.2);
  transition: 0.3s ease;
}

.music-btn:hover {
  background: #106db3;
  transform: scale(1.05);
}
