.quiz-container {
  text-align: center;
  margin-top: 40px;
}

.options {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #45a049;
}

.progress-bar {
  height: 10px;
  background-color: #2196F3;
  border-radius: 5px;
  margin: 20px auto;
  width: 80%;
  transition: width 0.5s ease;
}
.level-intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #fff;
  background: linear-gradient(120deg, #2196f3, #00bcd4);
  font-family: "Comic Sans MS", cursive;
  text-align: center;
  animation: fadeIntro 2s ease-in-out;
}

@keyframes fadeIntro {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.ask-btn {
  margin-top: 1rem;
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  border-radius: 8px;
  background: linear-gradient(45deg, #4caf50, #81c784);
  color: white;
  border: none;
  cursor: pointer;
  transition: 0.3s;
}
.ask-btn:hover {
  transform: scale(1.05);
  background: linear-gradient(45deg, #43a047, #66bb6a);
}
